#app {
  max-width: 330px;
  margin: 20px auto;
  background: #41b4a4;
  padding: 10px;
  position: relative;
  left: 0;
  right: 0;
  transform: translateY(20%);
}
input {
  background: black;
  border: 1px solid grey;
  border-radius: 5px;
  color: white;
  outline: none;
  width: 84%;
  padding: 20px;
}
#oper {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}
button {
  color: #ffffff;
  background: #85dcba;
  border: none;
  padding: 13px;
  margin: 6px 0;
  border-radius: 5px;
}
#numbers {
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}
#numbers button {
  padding: 25px;
  border-radius: 5px;
}
